.typography__heading_h1,
.typography__heading_h2,
.typography__heading_h3,
.typography__heading_h4{
  line-height: 1.2;
  margin: 0;
  padding: 0;
}

.typography__heading_h1 {
  font-size: var(--font__fontsize__72pt);
  font-weight: var(--font__fontweights__base-bold);
}

.typography__heading_h2 {
  font-size: var(--font__fontsize__52pt);
  font-weight: var(--font__fontweights__base-bold);
}

.typography__heading_h3 {
  font-size: var(--font__fontsize__38pt);
  line-height: 1.3;
  font-weight: var(--font__fontweights__base-regular);
}

.typography__heading_h4 {
  font-size: var(--font__fontsize__24pt);
  line-height: 1.3;
  font-weight: var(--font__fontweights__base-regular);
}

.typography__heading_h5 {
  font-size: var(--font__fontsize__18pt);
}

.typography__subtitle {
  font-size: var(--font__fontsize__16pt);
  line-height: 1.3;
  font-weight: var(--font__fontweights__base-regular);
}

.typography__body {
  font-size: var(--font__fontsize__14pt);
  color: var(--color__fg__rare);
}

.typography__content {
  font-size: var(--font__fontsize__14pt);
  line-height: 1.3;
  color: var(--color__fg__subtle);
}

.typography__mark {
  color: var(--color__accent__brand);
  background: none;
}

.typography__centered {
  text-align: center;
}

.typography__heavy {
  font-weight: bold;
}
