.footer {
  height: 90vh;
  background-color: #07070B;
  padding-top: calc(var(--space__space-base) * 20);
  text-align: center;
  margin-top: calc(var(--space__space-base) * 20)
}

.footer__layout {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.footer__form,
.footer__container {
  flex-grow: 1;
  align-items: center;
}

.footer__form {
  padding: calc(var(--space__space-base) * 7) 0;
  width: 100%;
}

.footer__form-controls {
  display: flex;
  width: 100%;
  display: flex;
  align-items: center;
  gap: 1em;
}

.footer__form-controls-input {
  flex-grow: 1;
}

.footer__navigation {
  padding: calc(var(--space__space-base) * 10) 0;
  position: relative;
}

.footer__navigation::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  height: 2px;
  background-image: linear-gradient(90deg, transparent, var(--color__accent__brand), transparent);
}

.footer__content {
  height: 100%;
  width: 100%;
  background-position: center 65%;
  background-repeat: no-repeat;
  background-image: url(../img/footer-logo.svg);
  display: flex;
  flex-direction: column;
}

.footer__linklist {
  width: 100%;
  justify-content: space-around;
  display: flex;
}

.footer__linkitem:last-child {
  display: none;
}

@media (min-width: 820px) {
  .footer__linkitem:last-child {
    display: block;
  }

  .footer__form {
    width: 50%;
  }
}
