html {
  width: 100vw;
}

body {
  padding: 0;
  margin: 0;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  font-family: Manrope, -apple-system, BlinkMacSystemFont,
    "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans",
    "Droid Sans", "Helvetica Neue", sans-serif;
  scroll-behavior: smooth;
  background-color: var(--color__bg__canvas);
  color: var(--color__fg__default);
  min-width: 520px;
  width: 100%;
  overflow-x: hidden;
  background-repeat: space;
  background-attachment: local;
  background-image:
    url(../img/backgrounds/noise.png),
    radial-gradient(
      ellipse
      1000px 1000px
      at 50% 500px,
      var(--color__accent__middle),
      transparent
    );
}

@font-face {
  font-family: 'Roboto Mono';
  src: url('../fonts/RobotoMono-Regular.woff2') format('woff2'), url('../fonts/RobotoMono-Regular.woff') format('woff');
  font-weight: 400;
  font-style: normal;
  font-display: swap;
}

@font-face {
  font-family: 'Roboto Mono';
  src: url('../fonts/RobotoMono-Regular.woff2') format('woff2'),
      url('../fonts/RobotoMono-Regular.woff') format('woff');
  font-weight: 400;
  font-style: normal;
  font-display: swap;
}

@font-face {
  font-family: 'Manrope';
  src: url('../fonts/Manrope-Regular.woff2') format('woff2'),
      url('../fonts/Manrope-Regular.woff') format('woff');
  font-weight: 400;
  font-style: normal;
  font-display: swap;
}

@font-face {
  font-family: 'Manrope';
  src: url('../fonts/Manrope-Bold.woff2') format('woff2'),
      url('../fonts/Manrope-Bold.woff') format('woff');
  font-weight: 700;
  font-style: normal;
  font-display: swap;
}

@font-face {
  font-family: 'Manrope';
  src: url('../fonts/Manrope-Light.woff2') format('woff2'),
      url('../fonts/Manrope-Light.woff') format('woff');
  font-weight: 300;
  font-style: normal;
  font-display: swap;
}

@font-face {
  font-family: 'Manrope';
  src: url('../fonts/Manrope-SemiBold.woff2') format('woff2'),
      url('../fonts/Manrope-SemiBold.woff') format('woff');
  font-weight: 600;
  font-style: normal;
  font-display: swap;
}

ul {
  list-style: none;
  padding: 0;
  margin: 0;
}

a {
  text-decoration: none;
  color: inherit;
}

.blur {
  -webkit-backdrop-filter: blur(8px);
  backdrop-filter: blur(8px);
}

.glow {
  fill: radial-gradient(100% 100% at 50% 0%, rgba(78, 114, 204, 0.25) 0%, rgba(78, 114, 204, 0) 100%), rgba(36, 37, 45, 0.3);
  
  background: radial-gradient(100% 100% at 50% 0%, rgba(78, 114, 204, 0.25) 0%, rgba(78, 114, 204, 0) 100%), rgba(36, 37, 45, 0.3);
  box-shadow: 0px 12px 24px rgba(0, 0, 0, 0.15);
}

.glow_bottom {
  background: radial-gradient(100% 100% at 50% 100%, rgba(78, 114, 204, 0.25) 0%, rgba(78, 114, 204, 0) 100%), rgba(36, 37, 45, 0.3);
  box-shadow: 0px 12px 24px rgba(0, 0, 0, 0.15);
}

.spot {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 500px;
  height: 500px;
  background: radial-gradient(100% 100% at 50% 150%, var(--color__accent__brand) 0%, transparent 100%);
}

.label {
  background: rgba(78, 114, 204, 0.1);
  border-radius: calc(var(--space__space-base) * 5) / 50%;
  padding: calc(var(--space__space-base) * 3) calc(var(--space__space-base) * 4);
  font-size: var(--font__fontsize__14pt);
  line-height: 1;
  display: inline-block;
}

.label.primary {
  background-color: var(--color__accent__rare);
  color: var(--color__fg__default);
}

.input {
  border-radius: 8px;
  padding: calc(var(--border_radius__radius-base) * 3) calc(var(--border_radius__radius-base) * 4);
  color: var(--color__content__input__middle);
  background-color: var(--color__input__default);
  border-width: 1px;
  border-style: solid;
  border-color: transparent transparent var(--color__input__border__default) transparent;
  outline: none;
}
.input:hover {
  border-bottom-color: var(--color__input__border__hover);
}

.input:active {
  border-bottom-color: var(--color__input__border__focus);
}

.button {
  background-color: var(--color__button__primary__default);
  border-radius: calc(var(--border_radius__radius-base) * 2);
  border: 0;
  padding: calc(var(--space__space-base) * 4) calc(var(--space__space-base) * 6);
  font-size: var(--font__fontsize__16pt);
  line-height: 1;
  color: var(--color__fg__default);
  display: inline-block;
  transition: background-color .3s;
}

.button.small {
  padding: calc(var(--space__space-base) * 2) calc(var(--space__space-base) * 3);
  font-size: var(--font__fontsize__14pt);
}

.button.additional {
  background-color: var(--color__button__second__default);
}

.button:hover {
  background-color: var(--color__button__primary__hover);
}

.button:active {
  background-color: var(--color__button__primary__presed);
}

.button.additional:hover {
  background-color: var(--color__button__second__hover);
}

.button.additional:active {
  background-color: var(--color__button__second__presed);
}