.team {
  background-image:
    radial-gradient(
      ellipse
      70% 50%
      at 50% 50%,
      var(--color__accent__middle),
      transparent
    );
}

.team__grid {
  gap: 1.5em;
  margin-top: calc(var(--space__space-base) * 20)
}

.team__tile {
  border-radius: calc(var(--space__space-base) * 14);
  background-color: var(--color__bg__canvas_opaque);
  position: relative;
  border: 1px solid var(--color__accent__middle);
  display: grid;
  flex-direction: column;
  box-sizing: border-box;
  overflow: hidden;
  grid-column: span 12;
  padding: calc(var(--space__space-base) * 5) calc(var(--space__space-base) * 8) calc(var(--space__space-base) * 8) calc(var(--space__space-base) * 5);
  color: var(--color__fg__rare);
  gap: 1em;
  grid-template: 
    "l t" calc(var(--space__space-base) * 20)
    "l p"
    "l c" calc(var(--space__space-base) * 10);
  opacity: 0;
  transform: scale(.8);
  transition: all .3s;
}

.js-intersecting .team__tile {
  opacity: 1;
  transform: scale(1);
}

.team__tile:nth-child(2) {
  transition-delay: .1s;
}
.team__tile:nth-child(3) {
  transition-delay: .2s;
}
.team__tile:nth-child(4) {
  transition-delay: .4s;
}
.team__tile:nth-child(5) {
  transition-delay: .6s;
}

.team__tile:nth-child(6) {
  transition-delay: .8s;
}

.team__tile-avatar {
  border-radius: 50%;
  width: calc(var(--space__space-base) * 19);
  height: calc(var(--space__space-base) * 19);
  overflow: hidden;
  grid-area: l;
}

.team__tile-avatar img {
  width: 100%;
}

.team__tile-creds {
  grid-area: t;
  align-self: center;
  display: flex;
  flex-direction: column;
  gap: 0.5em;
}

.team__tile-bio {
  grid-area: p;
  font-size: var(--font__fontsize__18pt);
  line-height: 1.5;
}

.team__tile-contacts {
  grid-area: c;
  align-self: center;
}

.team__tile-contacts-linkedin {
  background-image: url(../img/logos/linkedin.svg);
  background-repeat: no-repeat;
  background-position: center right;
}

.team__tile-contacts-account {
  font-family: 'Roboto Mono';
  font-size: var(--font__fontsize__18pt);
}

.team__tile-contacts-red {
  color: var(--color__bots_color__bot_2);
}

.team__tile-contacts-yellow {
  color: var(--color__bots_color__bot_4);
}

.team__tile-contacts-blue {
  color: var(--color__accent__brand);
}

.team__tile-contacts-green {
  color: var(--color__bots_color__bot_3)
}

@media (min-width: 820px) {
  .team__tile {
    grid-column: span 4;
  }
}
