.hero {
  margin-top: calc(var(--space__space-base) * 39);
  text-align: center;
}

.hero__action {
  margin-top: calc(var(--space__space-base) * 16);
}

.hero__dashboard {
  width: 100%;

  border-radius: calc(var(--space__space-base) * 6);
  margin-top: calc(var(--space__space-base) * 30);
  padding: var(--space__space-x05);
  background: linear-gradient(180deg, var(--color__accent__brand) , transparent);
  position: relative;
  transition: all 1s;
  opacity: 0;
}

.hero__dashboard__content {
  overflow: hidden;
  display: grid;
  gap: 1em;
  border-radius: calc(var(--space__space-base) * 6);
  background: linear-gradient(180deg, var(--color__bg__canvas) 10%, var(--color__bg__canvas) 50%, transparent);
  padding-right: calc(var(--space__space-base) * 4);
  box-sizing: border-box;
  grid-auto-columns: calc(var(--space__space-base) * 50) 1fr 1fr 1fr 1fr;
  grid-template: 
    "a search search search search" calc(var(--space__space-base) * 25)
    "a status combo bots users" calc(var(--space__space-base) * 20)
    "a requests requests requests requests" 1fr
    "a types types types types" 1fr;

}


.hero__dashboard img {
  width: 100%;
}

.hero__dashboard__search {
  padding-top: calc(var(--space__space-base) * 4);
  grid-area: search;
}

.hero__dashboard__aside {
  grid-area: a;
}

.hero__dashboard__search,
.hero__dashboard__aside {
  opacity: 0;
  transition: opacity .5s;
}

.hero__dashboard__topline-slot_all,
.hero__dashboard__topline-slot_status,
.hero__dashboard__topline-slot_bots,
.hero__dashboard__topline-slot_users {
  opacity: 0;
  transform: translate(0, 20%);
  transition: all .5s;
}

.hero__dashboard__topline-slot_all {
  grid-area: combo;
  transition-delay: .6s;
}

.hero__dashboard__topline-slot_status {
  grid-area: status;
  transition-delay: .3s;
}

.hero__dashboard__topline-slot_bots {
  grid-area: bots;
  transition-delay: .9s;
}

.hero__dashboard__topline-slot_users {
  grid-area: users;
  transition-delay: 1.2s;
}


.hero__dashboard__requests,
.hero__dashboard__types {
  opacity: 0;
  transform: translate(0, 20%);
  transition: all .5s;
}

.hero__dashboard__requests {
  grid-area: requests;
  transition-delay: 1.5s;
}

.hero__dashboard__types {
  grid-area: types;
  transition-delay: 2s;
}


.js-intersecting.hero__dashboard {
  opacity: 1;
}

.js-intersecting .hero__dashboard__search,
.js-intersecting .hero__dashboard__aside {
  opacity: 1;
  transition-delay: .4s;
}


.js-intersecting .hero__dashboard__requests,
.js-intersecting .hero__dashboard__types,
.js-intersecting .hero__dashboard__topline-slot_all,
.js-intersecting .hero__dashboard__topline-slot_status,
.js-intersecting .hero__dashboard__topline-slot_bots,
.js-intersecting .hero__dashboard__topline-slot_users {
  opacity: 1;
  transform: translate(0, 0);
}


