.js-intersecting .hero__topline-block {
  opacity: 1;
  transform: scale(1);
}

.js-intersecting .hero__chart-block {
  opacity: 1;
  transform: scale(1) translate(0, 0);
}

.hero__topline-block-user {
  transition-delay: 0;
}

.hero__topline-block-all {
  transition-delay: 200ms;
}

.hero__topline-block-bots {
  transition-delay: 400ms;
}

.hero__topline-block-users {
  transition-delay: 600ms;
}

.hero__chart-block-stats {
  transition-delay: 500ms;
}

.hero__chart-block-types {
  transition-delay: 500ms;
}
