.features__grid {
  gap: 1.5em;
  margin-top: calc(var(--space__space-base) * 18);
}

.features {
  padding: 50px 0;
  position: relative;
  background: url(../img/backgrounds/grid.svg) right bottom;
  background-size: 70%;
  position: relative;
  padding-bottom: calc(var(--space__space-base) * 55);
  background-repeat: no-repeat;
}

.features::after {
  background: url(../img/horizont-top.svg);
  transform: rotateX(180deg);
  content: '';
  position: absolute;
  height: 350px;
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center bottom;
  left: 0;
  right: 0;
  bottom: 100px;
}

.features__tile {
  min-height: calc(var(--space__space-base) * 90);
  border-radius: calc(var(--space__space-base) * 14);
  background-color: var(--color__bg__canvas_opaque);
  position: relative;
  border: 1px solid var(--color__accent__middle);
  display: flex;
  flex-direction: column;
  box-sizing: border-box;
  background-repeat: no-repeat;
  background-size: contain;
  overflow: hidden;
  opacity: 0;
  transition: all .3s;
}

.features__tile_wide,
.features__tile_narrow {
  grid-column: span 12;
}

.features__tile-content {
  width: 100%;
  height: 100%;
  background-repeat: no-repeat;
  background-size: contain;
  box-sizing: border-box;
  padding: calc(var(--space__space-base) * 14) calc(var(--space__space-base) * 7);
  display: flex;
  flex-direction: column;
  align-items: center;
}

.features__detect {
  transform: translate(-50%, 0);
}

.features__detect .features__tile-content {
  background-image: url(../img/features/detect.webp);
  background-size: 80%;
  background-position: center 25%;
  justify-content: flex-end;
}

.features__chart {
  transform: translate(50%, 0);
  transition-delay: .1s;
}

.features__chart .features__tile-content {
  background-image: url(../img/features/chart.webp);
  background-position: center bottom;
}

.features__ban {
  transform: translate(-50%, 0);
  transition-delay: .3s;
}

.features__ban .features__tile-content {
  background-image: url(../img/features/ban.webp);
  background-size: 60%;
  background-position: center 95%;
}

.features__plugin {
  transform: translate(50%, 0);
  transition-delay: .6s;
}

.features__plugin .features__tile-content {
  background-image: url(../img/features/plugin.webp);
  background-size: 95%;
  background-position: bottom right;
}

.js-intersecting .features__tile {
  opacity: 1;
  transform: translate(0, 0);
}

.features__cards {
  margin-top: calc(var(--space__space-base) * 20);
}

.features__card {
  background-repeat: no-repeat;
  background-size: 65%;
  margin-bottom: calc(var(--space__space-base) * 10);
  opacity: 0;
  transition: all .5s;
  transform: translate(0, 30%);
}

.js-intersecting .features__card {
  opacity: 1;
  transform: translate(0, 0);
}

.features__card-content {
  display: flex;
  flex-direction: column;
  gap: 2em;
  align-items: flex-start;
  grid-column: span 12;
}

.features__card-image {
  width: 100%;
  grid-column: span 12;
  margin-top: 2em;
}

@media (min-width: 820px) {
  .features__tile_wide {
    grid-column: span 8;
  }
  
  .features__tile_narrow {
    grid-column: span 4;
  }

  .features__card-content {
    grid-column: span 4;
  }
  
  .features__card-image {
    grid-column: 6 / span 7;
    margin-top: 0;
  }
}

@media (min-width: 820px) {
  .features {
    padding-bottom: calc(var(--space__space-base) * 90);
  }

  .features__tile {
    min-height: calc(var(--space__space-base) * 114);
  }

  .features__detect .features__tile-content {
    background-size: 60%;
  }
}