.layout__container {
  width: 80%;
  max-width: 1440px;
  margin: 0 auto;
}

.layout__grid {
  display: grid;
  grid-template-columns: repeat(12, 1fr);
  gap: var(--space__space-x4);
  margin: 0 auto;
  height: 100%;
}

.layout__grid_align_center {
  align-items: center;
}

.layout__narrow_only {
  grid-column: 2 / span 10;
}


.layout__section__subheading {
  margin-top: calc(var(--space__space-base) * 10);
}