.header {
  background-color: var(--color__bg__canvas_opaque);
  position: sticky;
  top: 0;
  padding: calc(var(--space__space-base) * 4) 0;
  z-index: var(--z-index-critical);
}

.header__grid,
.header__layout {
  align-items: center;
  height: 100%;
}

.header__logo {
  grid-column: span 2;
}

.header__nav {
  grid-column: 4 / span 5;
  text-align: center;
}

.header__nav-links {
  gap: var(--space__space-x13);
  width: 100%;
  font-size: var(--space__space-x4);
  font-weight: var(--font__fontweights__base-semibold);
  display: none;
}

.header__nav-link {
  flex-grow: 1;
  opacity: .6;
  transition: opacity .3s;
}

.header__nav-link:hover {
  opacity: 1;
}

.header__actions {
  grid-column: span 3;
  text-align: right;
}

.header__actions-links {
  display: inline-flex;
  gap: var(--space__space-4);
}

@media (min-width: 820px) {
  .header__nav-links {
    display: flex;
  }
}
