.concepts {
  background-image:
    url(../img/horizont-top.svg),
    url(../img/backgrounds/grid.svg),
    radial-gradient(
      ellipse
      50% 40%
      at 50% 60%,
      var(--color__accent__middle),
      transparent
    );
  background-size: contain;
  background-position: center 0;
  padding-top: 25%;
  text-align: center;
  background-repeat: no-repeat;
  position: relative;
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.concepts__flow {
  width: 100%;
  height: 300px;
  transform: scale(1.1);
  opacity: 0;
  transition: opacity .3s;
}

.js-intersecting .concepts__flow {
  opacity: 1;
}

@media (min-width: 820px) {
  .concepts__flow {
    width: 100%;
    height: 500px;
    transform: scale(1);
  }
}